* {
  font-family: 'Roboto', sans-serif; }

$first-color: #1eacc7;
$second-cololr: #ff5656;
header {
  position: relative;
  height: 50px; }

nav {
  position: fixed;
  width: 100%;
  height: 50px;
  background: #212121;
  z-index: 2; }

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style-type: none;

  .menu_link {
    font-weight: 300;
    font-size: 15px;
    color: #FFFFFF;
    text-decoration: none; }

  .menu_item {
    border-right: 2px dotted black;
    padding-right: 28px; } }

.subheader {
  position: absolute;
  width: 100%;
  height: 87px;
  top: 50px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px; }

  .subheader_official {
    margin-top: 6px;
    font-weight: 100;
    font-size: 13px;
    color: #FFFFFF;
    opacity: 0.5; }

  .subheader_call {
    margin-top: 19px;
    font-weight: 300;
    font-size: 12px;
    text-align: right;
    color: #FFFFFF; }

  .subheader_phone {
    margin-top: 8px;
    font-weight: 400;
    font-size: 22px;
    display: block;
    align-items: center;
    text-align: right;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 12px; }

  .subheader_btn {
    margin-top: 19px;
    width: 172px;
    height: 46px;
    box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, $first-color 0%, #4ce2ff 100%);
    font-weight: 400;
    font-size: 14px;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.promo {
  min-height: 730px;
  background: url('../img/First-bg.jpg') center (center / cover) no-repeat;
  padding: 155px 0 135px 0;

  .promo_header {
    font-weight: 700;
    font-size: 60px;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 60px;
    text-align: center;
    margin: 0; }

  .promo_subheader {
    font-weight: 700;
    font-size: 36px;
    text-transform: uppercase;
    color: #FFFFFF;
    line-height: 36px;
    text-align: center;
    margin-top: 22px;
    margin: 0; }

  .promo_descr {
    margin-top: 23px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF; }

  .promo_btn {
    display: block;
    margin: 118px auto 0 auto;
    width: 259px;
    height: 63px;
    box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
    border: none;
    border-radius: 4px;
    background-image: linear-gradient(to top, $first-color 0%, #4ce2ff 100%);
    font-weight: 400;
    font-size: 18px;
    color: #FFFFFF;
    text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21); } }

.advantage {
  min-height: 950px;
  padding: 71px 0 65px 0; }

.label {
  position: relative;
  margin: 0 auto;
  width: 112px;
  height: 24px;
  padding: 5px 12px;
  background-color: #000;
  font-size: 11px;
  font-weight: 300;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  z-index: 2;
  &_white {
    background-color: #fff;
    color: #000; } }

.title {
  position: relative;
  margin-top: 38px;
  margin-bottom: 0;
  font-size: 38px;
  font-weight: 700;
  text-align: center;
  color: #222;
  text-transform: uppercase;
  z-index: 2;
  &_white {
    color: #fff;
    margin-top: 32px; } }

.subtitle {
  position: relative;
  font-size: 17px;
  font-weight: 400;
  text-align: center;
  color: #222;
  z-index: 2;
  &_white {
    color: #fff; } }


.advantage .advantage_item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  .advantage_circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 113px;
    height: 113px;
    background-color: $first-color;
    margin-right: 26px;
    border-radius: 100%; }
  .advantage_descr {
    width: 343px;
    .advantage_subtitle {
      line-height: 21px;
      color: #1eacc7;
      font-size: 17px;
      font-weight: 400; }

    .advantage_text {
      font-size: 15px;
      color: #202020;
      font-weight: 300;
      line-height: 21px; } } }

.mobile {
  min-height: 864px;
  padding: 48px 0 79px 0;
  background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
  .row {
    margin-top: 32px; }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
        background: url('../icons/mobile-1.png') center (center / cover) no-repeat; }
    &_2 {
        background: url('../icons/mobile-2.png') center (center / cover) no-repeat; }
    &_3 {
        background: url('../icons/mobile-3.png') center (center / cover) no-repeat; }
    &_4 {
        background: url('../icons/mobile-4.png') center (center / cover) no-repeat; }
    &_5 {
        background: url('../icons/mobile-5.png') center (center / cover) no-repeat; }
    &_6 {
        background: url('../icons/mobile-6.png') center (center / cover) no-repeat; }
    &_subtitle {
        font-size: 24px;
        font-weight: 400;
        text-align: center;
        color: #fff;
        text-transform: uppercase;
        line-height: 31px; }
    &_plus {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        bottom: 35px;
        left: 50%;
        transform: translateX(-50%);
        width: 26px;
        height: 26px;
        margin: 0 auto;
        border-radius: 100%;
        background-color: #000;
        cursor: pointer; } } }

.car {
  position: relative;
  min-height: 895px;
  background: url('../img/mobile_bg.png') center (center / cover) no-repeat;
  padding: 57px 0 138px 0;
  &_img {
    position: relative;
    display: block;
    margin: 56px auto 0 auto;
    z-index: 2; }
  &_descr {
    font-size: 17px;
    font-weight: 300;
    text-align: center;
    color: #222;
    line-height: 24px; }
  .black {
    position: absolute;
    top: 0;
    left: 0;
    background-color: #070716;
    width: 100%;
    height: 397px;
    z-index: 1; } }

